import { Dumbbell, Users, TrendingUp } from "lucide-react";

const AlmaxsportStats = () => {
  return (
    <section
      className="py-14 bg-blue-50 text-center text-gray-800"
      aria-label="آمار اولیه آلمکس اسپورت"
    >
      <article className="max-w-6xl mx-auto grid grid-cols-1 md:grid-cols-3 gap-8 px-4">
        <div>
          <Dumbbell className="w-10 h-10 mx-auto text-blue-600 mb-2" />
          <h3 className="text-2xl font-bold">30+</h3>
          <p>باشگاه مجهز در کمتر از یک سال</p>
        </div>
        <div>
          <Users className="w-10 h-10 mx-auto text-blue-600 mb-2" />
          <h3 className="text-2xl font-bold">80+</h3>
          <p>مشتری فعال از سراسر کشور</p>
        </div>
        <div>
          <TrendingUp className="w-10 h-10 mx-auto text-blue-600 mb-2" />
          <h3 className="text-2xl font-bold">رشد سریع</h3>
          <p>به‌عنوان برند نوظهور تجهیزات باشگاهی</p>
        </div>
      </article>
    </section>
  );
};

export default AlmaxsportStats;
